.section-auth {
    box-sizing: border-box;
    height: 90vh;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.3), rgba(232, 0, 123, 0.35)),url('../../assets/cinema.jpeg');
    
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.auth-wrapper {
    background-color: #f4f4f4;
    border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    position: relative;
    overflow: hidden;
    width: 768px;
    max-width: 100%;
    min-height: 480px;
}
form h2 {
    color:#747474;
}
.form-container form {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.form-container input {
    background: #eee;
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
}
button {
    border-radius: 20px;
    border: 1px solid var(--color-main);
    background: var(--color-main);
    color: #fff;
    font-size: 12px;
    padding: 12px 45px;
    letter-spacing: 1px;
    transition: transform 80ms ease-in;
}
button:active {
    transform: scale(0.95);
}
button:focus{
    outline: none;
}
button.ghost{
    background: transparent;
    border-color: #fff;
}
.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}
.signin-container {
    left: 0;
    width: 50%;
    z-index: 2;
}
.signup-container {
    left: 0;
    width: 50%;
    z-index: 1;
    opacity: 0;
}
.overlay-container{
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
}
.overlay{
    background-image: linear-gradient(45deg, var(--color-main), #ca569d);
    color: #fff;
    position: relative;
    height: 100%;
    left: -100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}
.overlay-panel {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 40px;
    height: 100%;
    width: 50%;
    text-align: center;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}
.overlay-right {
    right: 0;
    transform: translateX(0);
}
.overlay-left {
    transform: translateX(-20%);
}

/* Animation */

/* Move Signin to the right */
.auth-wrapper.right-panel-active .signin-container {
    transform: translateX(100%);
}

/* Move overlay to the left */
.auth-wrapper.right-panel-active .overlay-container {
    transform: translateX(-100%);
}

/* Bring signup over signin */
.auth-wrapper.right-panel-active .signup-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
}

/* Move Overlay back to right */
.auth-wrapper.right-panel-active .overlay {
    transform: translateX(50%);
}
.auth-wrapper.right-panel-active .overlay-left {
    transform: translateX(0%);
}
.auth-wrapper.right-panel-active .overlay-right {
    transform: translateX(20%);
}